"use client"

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { useRouter } from "next/navigation";
import * as React from "react";

import AppLayout from "@/components/app-layout";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage, FormDescription } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { mockSubjects } from "@/lib/mock-data";
import { useToast } from "@/hooks/use-toast";


const examFormSchema = z.object({
  subjects: z.array(z.string()).refine((value) => value.some((item) => item), {
    message: "You have to select at least one subject.",
  }),
  numQuestions: z.coerce.number().min(1, "Number of questions must be at least 1.").max(50, "Cannot generate more than 50 questions."),
  difficulty: z.string().optional(),
  language: z.string().optional(),
});


export default function NewExamPage() {
    const router = useRouter();
    const { toast } = useToast();
    const [subjects] = React.useState(mockSubjects);

    const form = useForm<z.infer<typeof examFormSchema>>({
        resolver: zodResolver(examFormSchema),
        defaultValues: {
            subjects: [],
            numQuestions: 10,
        },
    });

    function onSubmit(values: z.infer<typeof examFormSchema>) {
        console.log("Generating exam with values:", values);
        toast({
            title: "Generating Exam...",
            description: "Your custom exam is being prepared.",
        });
        // In a real app, you'd save this config and generate an exam ID
        const examId = "mock-exam-123";
        router.push(`/exams/${examId}`);
    }

    return (
        <AppLayout>
            <div className="max-w-2xl mx-auto">
                <div className="mb-6">
                    <h1 className="text-3xl font-bold font-headline">Generate New Exam</h1>
                    <p className="text-muted-foreground">
                        Create a custom practice exam tailored to your needs.
                    </p>
                </div>
                <Card>
                    <CardHeader>
                        <CardTitle>Exam Configuration</CardTitle>
                        <CardDescription>Select your preferences for the exam.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <Form {...form}>
                            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
                                <FormField
                                    control={form.control}
                                    name="subjects"
                                    render={() => (
                                        <FormItem>
                                        <div className="mb-4">
                                            <FormLabel className="text-base">Subjects</FormLabel>
                                            <FormDescription>
                                            Select the subjects to include in the exam.
                                            </FormDescription>
                                        </div>
                                        <div className="grid grid-cols-2 gap-4">
                                            {subjects.map((item) => (
                                                <FormField
                                                key={item.id}
                                                control={form.control}
                                                name="subjects"
                                                render={({ field }) => {
                                                    return (
                                                    <FormItem
                                                        key={item.id}
                                                        className="flex flex-row items-start space-x-3 space-y-0"
                                                    >
                                                        <FormControl>
                                                        <Checkbox
                                                            checked={field.value?.includes(item.name)}
                                                            onCheckedChange={(checked) => {
                                                            return checked
                                                                ? field.onChange([...field.value, item.name])
                                                                : field.onChange(
                                                                    field.value?.filter(
                                                                    (value) => value !== item.name
                                                                    )
                                                                )
                                                            }}
                                                        />
                                                        </FormControl>
                                                        <FormLabel className="font-normal">
                                                        {item.name}
                                                        </FormLabel>
                                                    </FormItem>
                                                    )
                                                }}
                                                />
                                            ))}
                                        </div>
                                        <FormMessage />
                                        </FormItem>
                                    )}
                                />

                                <FormField
                                    control={form.control}
                                    name="numQuestions"
                                    render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Number of Questions</FormLabel>
                                            <FormControl>
                                                <Input type="number" placeholder="e.g., 20" {...field} />
                                            </FormControl>
                                            <FormMessage />
                                        </FormItem>
                                    )}
                                />
                                
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                    <FormField
                                        control={form.control}
                                        name="difficulty"
                                        render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Difficulty (Optional)</FormLabel>
                                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                            <FormControl>
                                                <SelectTrigger><SelectValue placeholder="Any Difficulty" /></SelectTrigger>
                                            </FormControl>
                                            <SelectContent>
                                                <SelectItem value="any">Any Difficulty</SelectItem>
                                                <SelectItem value="easy">Easy</SelectItem>
                                                <SelectItem value="medium">Medium</SelectItem>
                                                <SelectItem value="hard">Hard</SelectItem>
                                            </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                        )}
                                    />
                                    <FormField
                                        control={form.control}
                                        name="language"
                                        render={({ field }) => (
                                        <FormItem>
                                            <FormLabel>Language (Optional)</FormLabel>
                                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                                            <FormControl>
                                                <SelectTrigger><SelectValue placeholder="Any Language" /></SelectTrigger>
                                            </FormControl>
                                            <SelectContent>
                                                <SelectItem value="any">Any Language</SelectItem>
                                                <SelectItem value="en">English</SelectItem>
                                                <SelectItem value="ar">Arabic</SelectItem>
                                            </SelectContent>
                                            </Select>
                                            <FormMessage />
                                        </FormItem>
                                        )}
                                    />
                                </div>
                                
                                <Button type="submit" size="lg" className="w-full">Generate Exam</Button>
                            </form>
                        </Form>
                    </CardContent>
                </Card>
            </div>
        </AppLayout>
    );
}
